<h2 id="client-side" tabindex="-1"><a class="header-anchor" href="#client-side" aria-hidden="true">#</a> Client-side</h2>
<h3 id="question-1-2p" tabindex="-1"><a class="header-anchor" href="#question-1-2p" aria-hidden="true">#</a> Question 1 (2p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>In an SPA, the content on the page should change when anchor elements are clicked. One SPA tries to make that happen with the code shown below.</p>
<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code><span class="token doctype"><span class="token punctuation">&lt;!</span><span class="token doctype-tag">DOCTYPE</span> <span class="token name">html</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>html</span> <span class="token attr-name">lang</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>en<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>head</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>meta</span> <span class="token attr-name">charset</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>UTF-8<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>title</span><span class="token punctuation">></span></span>SPA<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>title</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>script</span><span class="token punctuation">></span></span><span class="token script"><span class="token language-javascript">
        
        document<span class="token punctuation">.</span><span class="token function">addEventListener</span><span class="token punctuation">(</span><span class="token string">'DOMContentLoaded'</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
            
            <span class="token comment">// When clicking on an anchor element, change the content</span>
            <span class="token comment">// on the page.</span>
            <span class="token keyword">const</span> anchors <span class="token operator">=</span> document<span class="token punctuation">.</span><span class="token function">querySelectorAll</span><span class="token punctuation">(</span><span class="token string">'a'</span><span class="token punctuation">)</span>
            
            <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> anchor <span class="token keyword">of</span> anchors<span class="token punctuation">)</span><span class="token punctuation">{</span>
                anchor<span class="token punctuation">.</span><span class="token function">addEventListener</span><span class="token punctuation">(</span><span class="token string">'click'</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
                    <span class="token function">changeToPage</span><span class="token punctuation">(</span>anchor<span class="token punctuation">.</span><span class="token function">getAttribute</span><span class="token punctuation">(</span><span class="token string">'href'</span><span class="token punctuation">)</span><span class="token punctuation">)</span>
                <span class="token punctuation">}</span><span class="token punctuation">)</span>
            <span class="token punctuation">}</span>
            
            <span class="token comment">// Start by showing the start page.</span>
            <span class="token function">changeToPage</span><span class="token punctuation">(</span><span class="token string">'/'</span><span class="token punctuation">)</span>
            
        <span class="token punctuation">}</span><span class="token punctuation">)</span>
        
        <span class="token keyword">function</span> <span class="token function">changeToPage</span><span class="token punctuation">(</span><span class="token parameter">uri</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
            
            <span class="token comment">// Hide all pages.</span>
            <span class="token keyword">const</span> pageDivs <span class="token operator">=</span> document<span class="token punctuation">.</span><span class="token function">querySelectorAll</span><span class="token punctuation">(</span><span class="token string">'.page'</span><span class="token punctuation">)</span>
            <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> pageDiv <span class="token keyword">of</span> pageDivs<span class="token punctuation">)</span><span class="token punctuation">{</span>
                pageDiv<span class="token punctuation">.</span>style<span class="token punctuation">.</span>display <span class="token operator">=</span> <span class="token string">'none'</span>
            <span class="token punctuation">}</span>
            
            <span class="token comment">// Show the page associated with the given URI.</span>
            <span class="token keyword">let</span> pageId
            
            <span class="token keyword">switch</span><span class="token punctuation">(</span>uri<span class="token punctuation">)</span><span class="token punctuation">{</span>
                <span class="token keyword">case</span> <span class="token string">'/'</span><span class="token operator">:</span>
                    pageId <span class="token operator">=</span> <span class="token string">'start-page'</span>
                <span class="token keyword">break</span>
                <span class="token keyword">case</span> <span class="token string">'/about'</span><span class="token operator">:</span>
                    pageId <span class="token operator">=</span> <span class="token string">'about-page'</span>
                <span class="token keyword">break</span>
            <span class="token punctuation">}</span>
            
            document<span class="token punctuation">.</span><span class="token function">getElementById</span><span class="token punctuation">(</span>pageId<span class="token punctuation">)</span><span class="token punctuation">.</span>style<span class="token punctuation">.</span>display <span class="token operator">=</span> <span class="token string">'block'</span>
            
        <span class="token punctuation">}</span>
        
    </span></span><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>script</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>head</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>body</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>a</span> <span class="token attr-name">href</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>/<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>Home page<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>a</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>a</span> <span class="token attr-name">href</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>/about<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>About page<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>a</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>div</span> <span class="token attr-name">class</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>page<span class="token punctuation">"</span></span> <span class="token attr-name">id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>start-page<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
        This is the start page!
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>div</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>div</span> <span class="token attr-name">class</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>page<span class="token punctuation">"</span></span> <span class="token attr-name">id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>about-page<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
        This is the about page!
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>div</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>body</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>html</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br><span class="line-number">31</span><br><span class="line-number">32</span><br><span class="line-number">33</span><br><span class="line-number">34</span><br><span class="line-number">35</span><br><span class="line-number">36</span><br><span class="line-number">37</span><br><span class="line-number">38</span><br><span class="line-number">39</span><br><span class="line-number">40</span><br><span class="line-number">41</span><br><span class="line-number">42</span><br><span class="line-number">43</span><br><span class="line-number">44</span><br><span class="line-number">45</span><br><span class="line-number">46</span><br><span class="line-number">47</span><br><span class="line-number">48</span><br><span class="line-number">49</span><br><span class="line-number">50</span><br><span class="line-number">51</span><br><span class="line-number">52</span><br><span class="line-number">53</span><br><span class="line-number">54</span><br><span class="line-number">55</span><br><span class="line-number">56</span><br><span class="line-number">57</span><br><span class="line-number">58</span><br><span class="line-number">59</span><br><span class="line-number">60</span><br><span class="line-number">61</span><br></div></div><ol>
<li>When testing the SPA, changing the page content when clicking on one of the two anchors doesn't work. Why? And what in the code needs to be changed to make it work?</li>
<li>After having fixed the problem above and uploaded the SPA to a server (for example one running NGINX) without any additional configuration (i.e. a <code>GET</code> request for <code>/</code> send back the SPA above), users start complaining about that bookmarking pages and the back button in the web browser don't work. Explain what you changes you need to do to the code and the server to make these things work. No need to mention implementation details, just list the changes that needs to be made.</li>
</ol>
</template>
<template v-slot:sample-answer>
<ol>
<li>The web browser's default behavior when a link is clicked is to fetch the webpage the link leads to. That happens now, so the web browser loads a new page. we don't want that default behavior, and must therefor tell the web browser to not do it by adding the <code>event</code> parameter to our <code>click</code> handler function, and in it call <code>event.preventDefault()</code></li>
<li>
<ul>
<li>Configure the server to always send back the webpage above no matter if the URI is <code>/</code> or <code>/about</code></li>
<li>Instead of hard coding <code>/</code> in <code>changeToPage('/')</code>, we should pass it the actual URI the user is using</li>
<li>When a link is clicked, we need to tell the web browser to create a new history entry with the new URI</li>
<li>We need to listen for when the user changes the current history entry (i.e. when the user clicks on the back/forward button), and when that happen call <code>changeToPage()</code> and pass it the URI in the new current history entry</li>
</ul>
</li>
</ol>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.5 points for problem on (1)</li>
<li>0.5 points for solution to (1)</li>
<li>0.25 points for each correct mentioned list item in (2)</li>
</ul>
</template>
</ExamQuestion>
