<h3 id="question-4-1p" tabindex="-1"><a class="header-anchor" href="#question-4-1p" aria-hidden="true">#</a> Question 4 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>The two code pieces below don't have the same functionality. Explain what the practical difference between them is.</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>document<span class="token punctuation">.</span><span class="token function">addEventListener</span><span class="token punctuation">(</span><span class="token string">'DOMContentLoaded'</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token keyword">const</span> anchors <span class="token operator">=</span> document<span class="token punctuation">.</span><span class="token function">querySelector</span><span class="token punctuation">(</span><span class="token string">'a'</span><span class="token punctuation">)</span>
    
    <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> anchor <span class="token keyword">of</span> anchors<span class="token punctuation">)</span><span class="token punctuation">{</span>
        anchor<span class="token punctuation">.</span><span class="token function">addEventListener</span><span class="token punctuation">(</span><span class="token string">'click'</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">event</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
            <span class="token function">onAnchorClicked</span><span class="token punctuation">(</span>anchor<span class="token punctuation">)</span>
        <span class="token punctuation">}</span><span class="token punctuation">)</span>
    <span class="token punctuation">}</span>
    
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>document<span class="token punctuation">.</span><span class="token function">addEventListener</span><span class="token punctuation">(</span><span class="token string">'DOMContentLoaded'</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    document<span class="token punctuation">.</span>body<span class="token punctuation">.</span><span class="token function">addEventListener</span><span class="token punctuation">(</span><span class="token string">'click'</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">event</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
        
        <span class="token comment">// event.target.closest('a') finds the anchor element closest to</span>
        <span class="token comment">// the element the user clicked on (either the element itself, or</span>
        <span class="token comment">// one of its ancestors (parent, or parent's parent, etc.), or null,</span>
        <span class="token comment">// if the element itself and none of its parents are an anchor element).</span>
        <span class="token keyword">const</span> anchor <span class="token operator">=</span> event<span class="token punctuation">.</span>target<span class="token punctuation">.</span><span class="token function">closest</span><span class="token punctuation">(</span><span class="token string">'a'</span><span class="token punctuation">)</span>
        
        <span class="token keyword">if</span><span class="token punctuation">(</span>anchor <span class="token operator">!=</span> <span class="token keyword">null</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
            <span class="token function">onAnchorClicked</span><span class="token punctuation">(</span>anchor<span class="token punctuation">)</span>
        <span class="token punctuation">}</span>
        
    <span class="token punctuation">}</span><span class="token punctuation">)</span>
    
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br></div></div><p><strong>Note</strong>: Do not explain how the code works, but do explain how their functionality differ. That is a very important difference on a dynamic webpage.</p>
</template>
<template v-slot:sample-answer>
<p>The second code piece also catches clicks on anchor elements added to the DOM after the <code>DOMContentLoaded</code> event fires.</p>
<p>Below is a longer explanation for the ones who don't understand.</p>
<p>The first code piece listens for clicks on all anchor elements that exist when the <code>DOMContentLoaded</code> event fires. If anchor elements are added to the DOM after that, clicks on those anchor elements won't fire our click listener.</p>
<p>The second code piece will catch clicks on any element, and then check if that element (or one of its ancestors) is an anchor element, and if so fire our click listeners. This way we don't only listen to clicks on anchor elements that exist when the <code>DOMContentLoaded</code> event fires, but also on anchor elements that are added to the DOM afterwards.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for correct explanation of the difference (&quot;The second code piece also catches clicks on anchor elements added to the DOM after the <code>DOMContentLoaded</code> event fires.&quot;)
<ul>
<li>(or 0.5 points for explaining how the code works, but not mentioning &quot;The second code piece also catches clicks on anchor elements added to the DOM after the <code>DOMContentLoaded</code> event fires.&quot;)</li>
</ul>
</li>
</ul>
</template>
</ExamQuestion>
<h2 id="containers" tabindex="-1"><a class="header-anchor" href="#containers" aria-hidden="true">#</a> Containers</h2>
<h3 id="question-5-1p" tabindex="-1"><a class="header-anchor" href="#question-5-1p" aria-hidden="true">#</a> Question 5 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>In Docker, explain what port mapping is, and why developers often need to use it.</p>
</template>
<template v-slot:sample-answer>
<p>A container in Docker does by default have all its ports closed, so nothing from outside the container can connect to the applications running in the container. Using port mapping, a programmer can tell the host computer that some ports on the host computer, like port <code>8000</code>, should lead to ports in the container, like port <code>1000</code>. The programmer can this way, for example, use port <code>8000</code> on the host computer to connect to port <code>1000</code> on the container.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.5 points for ports on host machine maps to ports on container
<ul>
<li>(only 0.25 points for not being clear about when talking about ports on host machine and when talking about port on the container)</li>
</ul>
</li>
<li>0.5 points for ports on containers are closed by default/only way to communicate with the app running in the container from the host machine</li>
</ul>
</template>
</ExamQuestion>
<h2 id="scaling" tabindex="-1"><a class="header-anchor" href="#scaling" aria-hidden="true">#</a> Scaling</h2>
<h3 id="question-6-2p" tabindex="-1"><a class="header-anchor" href="#question-6-2p" aria-hidden="true">#</a> Question 6 (2p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Explain the difference between <em>horizontal scaling</em> and <em>vertical scaling</em>.</p>
<p>If you don't think about scaling when implementing your application, which scaling approach will be easiest for you to apply? Justify your answer.</p>
</template>
<template v-slot:sample-answer>
<p>Vertical scaling is when you run your application on a more powerful computer (faster CPU, more RAM, more storage memory, etc.). If you don't think about scaling, using this scaling approach is easiest, because you don't need to think about anything special when you implement your application.</p>
<p>Horizontal scaling is when you run your application on multiple computers. This scaling approach is harder, because you need to make sure that your application is stateless, and that all your application instances share the same state (for example store the state in a database all application instances are connected to instead of storing it in global variables).</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.5 points for description of vertical scaling</li>
<li>0.5 points for description of horizontal scaling</li>
<li>1 point for justifying why horizontal scaling is harder
<ul>
<li>(many use <em>sessions</em> as argument for why horizontal scaling is harder, but using sticky sessions, you don't need to change any of the code in your application, so not really a valid argument, but I have accepted it to some degree anyway)</li>
</ul>
</li>
</ul>
</template>
</ExamQuestion>
<h2 id="server-side" tabindex="-1"><a class="header-anchor" href="#server-side" aria-hidden="true">#</a> Server-side</h2>
<h3 id="question-7-3p" tabindex="-1"><a class="header-anchor" href="#question-7-3p" aria-hidden="true">#</a> Question 7 (3p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Explain what a three-layered architecture is, which parts it consists of and what each part is used for. Then also explain how web applications use a three-layered architecture.</p>
<p>Don't write any code.</p>
</template>
<template v-slot:sample-answer>
<p>A three-layered architecture is a way organize the code of an application in three different layers, each with their own responsibilities:</p>
<ul>
<li>The <em>Data Access Layer</em> contains the code for storing and retrieving the application's data. It usually provides CRUD (Create, Read, Update and Delete) operations for this</li>
<li>The <em>Business Logic Layer</em> contains the code for carrying out the tasks the user wants to do (i.e. the application's functionality), such as computing how much the user should pay for the products put in a shopping bag, verify payments, etc. It typically contains authorization checks (for example, only allowing the user to use the application if the user has a paid membership or if the test period is not over yet) and validation checks (for example, checking if the data the user sends to the application is in correct format). Is uses the Data Access Layer when needed</li>
<li>The <em>Presentation Layer</em> is responsible for implementing an interface through which the application can be used, for example a graphical user interface that humans can use. When the user interacts with the application through the interface, for example clicks on a button, the Presentation Layer asks the Business Logic Layer to execute the task that is associated with the button</li>
</ul>
<p>In web applications, the Presentation Layer is responsible for receiving HTTP requests, and then ask the Business Logic Layer to carry out the requests. The Data Access Layer often store the data in a database. The Presentation Layer is also responsible for generating the HTTP response to send back, containing the HTML and CSS code the web browser use to display the webpage in the response to the user.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.5 points for what it is</li>
<li>0.25 points for what each part's name is</li>
<li>0.25 points for what each part is used for</li>
<li>0.5 points for how web app use it (HTTP, HTML, CSS in Presentation Layer, and description of the other layers)</li>
<li>0.5 points for good quality answers (many details, not using different names to describe the same thing, using the correct name on things, write proper and easy to read sentences, clearly explain which layer making use of which other layer, use singular and plural correctly, etc.)</li>
</ul>
<div class="custom-container warning"><p class="custom-container-title">Note!</p>
<p>Answering <em>Explain what a three-layered architecture is</em> with something like <em>It's an architecture</em>, or <em>It's an architecture consisting of three layers</em> is of course not a good way to do it. Anyone gets that understanding by just reading the name. You have to explain what it's an architecture of, and what that means (what is a layer? what does a layer consist of? Etc.).</p>
<p>The Presentation Layer doesn't have to make use of HTML and HTTP. This architecture can not only be used in web applications, but other type of applications too.</p>
<p>The Data Access Layer does not have to store data in a database. It's not called <em>Database Access Layer</em>.</p>
<p>Some students write the layers are independent of each other/know nothing about each others. That is not true. If it would be true, then would, for example, the Presentation Layer have no way of using the Business Logic Layer.</p>
<p>Many students write that the Presentation Layer receives HTTP requests, and then sends the requests to the Business Logic Layer. It really sounds like you're saying <em>the Business Logic Layer obtains the HTTP request</em>, and that is wrong; the Business Logic Layer shouldn't know the Presentation Layer makes use of HTTP at all, and therefor it can't receive HTTP requests from it. Saying that the Business Logic Layers <em>carries out</em> the request, or similar, is OK, because that does not imply the Business Logic Layers receives the HTTP request.</p>
</div>
</template>
</ExamQuestion>
<h3 id="question-8-1p" tabindex="-1"><a class="header-anchor" href="#question-8-1p" aria-hidden="true">#</a> Question 8 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>In a three-layered architecture, name or explain one security vulnerability in respective layer you need to think of.</p>
</template>
<template v-slot:sample-answer>
<ul>
<li>Presentation Layer: Cross-Site Scripting</li>
<li>Business Logic Layer: Broken Authorization</li>
<li>Data Access Layer: SQL Injection</li>
</ul>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.33 points for each correct name/explanation</li>
</ul>
</template>
</ExamQuestion>
