<h3 id="question-12-1p" tabindex="-1"><a class="header-anchor" href="#question-12-1p" aria-hidden="true">#</a> Question 12 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Does it make sense to use the <code>Content-Type</code> header in an HTTP <code>GET</code> request? Justify your answer.</p>
</template>
<template v-slot:sample-answer>
<p>No, the <code>Content-Type</code> header indicates which format the body of the request is written in, but <code>GET</code> requests do not send any resource to the server, and hence should not contain any body at all, so using the <code>Content-Type</code> header in a <code>GET</code> request makes no sense.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for correct answer and valid justification</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-13-3p" tabindex="-1"><a class="header-anchor" href="#question-13-3p" aria-hidden="true">#</a> Question 13 (3p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Explain how a JWT works. Be as detailed as possible.</p>
</template>
<template v-slot:sample-answer>
<p>A JSON Web Token consists of 3 parts in the format <code>AAA.BBB.CCC</code>.</p>
<p>The <code>AAA</code> part is called the header. It's a JSON object encoded in the Base64 format. The JSON object contains meta information about the token, such that it is a JSON Web Token and which hashing algorithm that is used to compute the <code>CCC</code> part.</p>
<p>The <code>BBB</code> part is called the payload. It contains the data the token represents (the claims). It's a JSON object encoded in the Base64 format.</p>
<p>The <code>CCC</code> part is called the signature, and it contains a hash value of the hashing algorithm defined in the header. Simply use the hashing algorithm on <code>AAA.BBB</code> and your own chosen salt, and you end up with the <code>CCC</code> part.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.5 points for <code>A.B.C</code> format
<ul>
<li>(only 0.25 points for saying it consists of 3 parts)</li>
</ul>
</li>
<li>0.33 points for respective part name</li>
<li>0.5 points for respective part description
<ul>
<li>(only 0.25 points for respective description not mentioning details like Base64/JSON)</li>
</ul>
</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-14-1p" tabindex="-1"><a class="header-anchor" href="#question-14-1p" aria-hidden="true">#</a> Question 14 (1p)</h3>
