<p>Yes, if that's something the application using the REST API needs to know about the logged in user, it makes sense. Most likely the application will show some extra GUI features if the logged in user is an admin, and if so it should be able to obtain that information through the ID token.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for correct answer and valid justification</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-15-1p" tabindex="-1"><a class="header-anchor" href="#question-15-1p" aria-hidden="true">#</a> Question 15 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
