<h2 id="security" tabindex="-1"><a class="header-anchor" href="#security" aria-hidden="true">#</a> Security</h2>
<h3 id="question-16-1p" tabindex="-1"><a class="header-anchor" href="#question-16-1p" aria-hidden="true">#</a> Question 16 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Explain what SOP is, and briefly explain how it works (i.e. what the difference is from not having SOP).</p>
</template>
<template v-slot:sample-answer>
<p>The Same-Origin Policy (SOP) is a policy web browsers follow to improve security. Without the policy, client-side code would be allowed to send any HTTP request to any web application it wants, but with the policy, client-side code coming from one web application (the &quot;origin&quot; of the code) is only allowed to communicate with (i.e. send HTTP requests to/read HTTP responses from) other web applications in a very limited way. In short, only <code>GET</code> and <code>POST</code> requests are allowed to be sent to other web applications, only a few different HTTP request headers are allowed to be used, and the client-side code is not allowed to read HTTP responses.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.5 points for a policy web browsers use to improve security</li>
<li>0.5 points for explaining the policy</li>
</ul>
</template>
</ExamQuestion>
<h3 id="question-17-2p" tabindex="-1"><a class="header-anchor" href="#question-17-2p" aria-hidden="true">#</a> Question 17 (2p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>Explain what CORS is, and explain how it works (be as detailed as possible).</p>
</template>
<template v-slot:sample-answer>
<p>Cross-Origin Resource Sharing is a way for client-side code to get around the restrictions web browsers enforce with SOP. When client-side code from one origin sends a HTTP request to another origin, the web browser can use CORS to check with the receiving server if it allows client-side from another origin to send HTTP requests to it.</p>
<p>For &quot;simple&quot; requests (requests from client-side code SOP allows to be sent to another origin, but it doesn't allow the client-side code to read the response), the web browser will send the HTTP request the client-side code wants to send, and if the server has added some HTTP headers to the response indicating the client-side code is allowed to read the response, the web browser will allow the client-side code to read the response.</p>
<p>For other requests from client-side code to another origin, the web browser will first send its own <code>OPTIONS</code> request (known as the preflight request), and in the response to that request the server can add HTTP headers to indicate if the client-side code should be allowed to send the request it wants to send or not, and if the headers indicate the client-side code is allowed to send it, the web browser will send the request, and then let the client-side code read the response.</p>
<p>For the server to indicate if the client-side code should be allowed to send the request/read the response, the following HTTP headers can be used:</p>
<ul>
<li><code>Access-Control-Allow-Origin</code>: The value indicates which origin the client-side code may have to accept sending the request</li>
<li><code>Access-Control-Allow-Methods</code>: The value indicates which method the client-side code may use in the cross-origin request</li>
<li><code>Access-Control-Allow-Headers</code>: The value indicates which headers the client-side code may use in the cross-origin request</li>
</ul>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.25 points for used by <strong>client-side code and web browsers</strong> (just writing <em>website</em> is not enough)</li>
<li>0.25 points for a workaround for SOP/sending cross-site requests</li>
<li>0.25 points for description of simple request</li>
<li>0.5 points for description of preflight request</li>
<li>0.25 points for each description (name not required) of a CORS header (max 3)</li>
</ul>
</template>
</ExamQuestion>
<h2 id="code-writing" tabindex="-1"><a class="header-anchor" href="#code-writing" aria-hidden="true">#</a> Code Writing</h2>
<h3 id="question-18-1p" tabindex="-1"><a class="header-anchor" href="#question-18-1p" aria-hidden="true">#</a> Question 18 (1p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>To represent a human in JavaScript, one can use an object like:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> human <span class="token operator">=</span> <span class="token punctuation">{</span>
    id<span class="token operator">:</span> <span class="token number">1</span><span class="token punctuation">,</span>
    name<span class="token operator">:</span> <span class="token string">"Alice"</span><span class="token punctuation">,</span>
    age<span class="token operator">:</span> <span class="token number">10</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><p>Implement the JavaScript function <code>getIdOfOldestHuman(humans)</code>, which receives an array with human objects, and returns the id of the oldest human. Return <code>-1</code> if there are no human objects in the array.</p>
</template>
<template v-slot:sample-answer>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">getIdOfOldestHuman</span><span class="token punctuation">(</span><span class="token parameter">humans</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token keyword">if</span><span class="token punctuation">(</span>humans<span class="token punctuation">.</span>length <span class="token operator">==</span> <span class="token number">0</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
        <span class="token keyword">return</span> <span class="token operator">-</span><span class="token number">1</span>
    <span class="token punctuation">}</span>
    
    <span class="token keyword">let</span> oldestHuman <span class="token operator">=</span> humans<span class="token punctuation">[</span><span class="token number">0</span><span class="token punctuation">]</span>
    
    <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> human <span class="token keyword">of</span> humans<span class="token punctuation">)</span><span class="token punctuation">{</span>
        <span class="token keyword">if</span><span class="token punctuation">(</span>oldestHuman<span class="token punctuation">.</span>age <span class="token operator">&lt;</span> human<span class="token punctuation">.</span>age<span class="token punctuation">)</span><span class="token punctuation">{</span>
            oldestHuman <span class="token operator">=</span> human
        <span class="token punctuation">}</span>
    <span class="token punctuation">}</span>
    
    <span class="token keyword">return</span> oldestHuman<span class="token punctuation">.</span>id
    
<span class="token punctuation">}</span>
