<span class="token keyword">const</span> ten <span class="token operator">=</span> <span class="token function">incrementBy1</span><span class="token punctuation">(</span><span class="token number">9</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div></template>
<template v-slot:sample-answer>
