<h3 id="question-20-4p" tabindex="-1"><a class="header-anchor" href="#question-20-4p" aria-hidden="true">#</a> Question 20 (4p)</h3>
<ExamQuestion>
<template v-slot:question>
<p>The function <code>getMovieByTitle(title, callback)</code> can be used to fetch a movie with a specific title asynchronously from the server like this:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token function">getMovieByTitle</span><span class="token punctuation">(</span><span class="token string">"Shrek"</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">error<span class="token punctuation">,</span> movie</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">if</span><span class="token punctuation">(</span>error<span class="token punctuation">)</span><span class="token punctuation">{</span>
        <span class="token comment">// Couldn't fetch the movie.</span>
    <span class="token punctuation">}</span><span class="token keyword">else</span><span class="token punctuation">{</span>
        <span class="token comment">// Do something with the movie.</span>
        <span class="token comment">// movie = {id: 3, title: "Shrek"}</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><p>Implement the function <code>getMoviesByTitles(titles, callback)</code>, which receives an array with movie titles and that should fetch the movies with those titles from the server, and send them back in an array by passing them to the callback function, or, if something goes wrong, pass the callback function the error it gets from <code>getMovieByTitle(title, callback)</code>.</p>
<p>Sample usage:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token function">getMoviesByTitles</span><span class="token punctuation">(</span><span class="token punctuation">[</span><span class="token string">"Shrek"</span><span class="token punctuation">,</span> <span class="token string">"Goldeneye"</span><span class="token punctuation">]</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">error<span class="token punctuation">,</span> movies</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">if</span><span class="token punctuation">(</span>error<span class="token punctuation">)</span><span class="token punctuation">{</span>
        <span class="token comment">// Couldn't fetch the movies.</span>
    <span class="token punctuation">}</span><span class="token keyword">else</span><span class="token punctuation">{</span>
        <span class="token comment">// Do something with the movies.</span>
        <span class="token comment">// movies = [</span>
        <span class="token comment">//   {id: 2, title: "Shrek"},</span>
        <span class="token comment">//   {id: 5, title: "Goldeneye"}</span>
        <span class="token comment">// ]</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><p>Important details:</p>
<ul>
<li>The callback function should be called only once</li>
<li>As soon as any function gives an error, the callback function should be called with that error (the other argument should be <code>null</code>)</li>
<li>If all movies are successfully fetched, the callback function should be called with <code>null</code> as the error, and the movies array as the second argument</li>
<li>The order of the movies in the movies array is not important</li>
</ul>
<p>Implement two different versions of the function:</p>
<ul>
<li>One version that fetches the movies in sequential order (one at a time)</li>
<li>One version that fetches the movies in parallel</li>
</ul>
</template>
<template v-slot:sample-answer>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">// Sequential</span>
<span class="token keyword">function</span> <span class="token function">getMoviesByTitles</span><span class="token punctuation">(</span><span class="token parameter">titles<span class="token punctuation">,</span> callback</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token keyword">const</span> fetchedMovies <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span>
    
    <span class="token function">fetchMovie</span><span class="token punctuation">(</span><span class="token number">0</span><span class="token punctuation">)</span>
    
    <span class="token keyword">function</span> <span class="token function">fetchMovie</span><span class="token punctuation">(</span><span class="token parameter">titleIndex</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
        
        <span class="token keyword">if</span><span class="token punctuation">(</span>titleIndex <span class="token operator">==</span> titles<span class="token punctuation">.</span>length<span class="token punctuation">)</span><span class="token punctuation">{</span>
            <span class="token function">callback</span><span class="token punctuation">(</span><span class="token keyword">null</span><span class="token punctuation">,</span> fetchedMovies<span class="token punctuation">)</span>
        <span class="token punctuation">}</span><span class="token keyword">else</span><span class="token punctuation">{</span>
            
            <span class="token function">getMovieByTitle</span><span class="token punctuation">(</span>titles<span class="token punctuation">[</span>titleIndex<span class="token punctuation">]</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">error<span class="token punctuation">,</span> movie</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
                
                <span class="token keyword">if</span><span class="token punctuation">(</span>error<span class="token punctuation">)</span><span class="token punctuation">{</span>
                    <span class="token function">callback</span><span class="token punctuation">(</span>error<span class="token punctuation">,</span> <span class="token keyword">null</span><span class="token punctuation">)</span>
                <span class="token punctuation">}</span><span class="token keyword">else</span><span class="token punctuation">{</span>
                    fetchedMovies<span class="token punctuation">.</span><span class="token function">push</span><span class="token punctuation">(</span>movie<span class="token punctuation">)</span>
                    <span class="token function">fetchMovie</span><span class="token punctuation">(</span>titleIndex <span class="token operator">+</span> <span class="token number">1</span><span class="token punctuation">)</span>
                <span class="token punctuation">}</span>
                
            <span class="token punctuation">}</span><span class="token punctuation">)</span>
            
        <span class="token punctuation">}</span>
        
    <span class="token punctuation">}</span>
    
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br></div></div><div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">// Parallel</span>
<span class="token keyword">function</span> <span class="token function">getMoviesByTitles</span><span class="token punctuation">(</span><span class="token parameter">titles<span class="token punctuation">,</span> callback</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token keyword">const</span> fetchedMovies <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span>
    <span class="token keyword">let</span> hasSentBackError <span class="token operator">=</span> <span class="token boolean">false</span>
    
    <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> title <span class="token keyword">of</span> titles<span class="token punctuation">)</span><span class="token punctuation">{</span>
        
        <span class="token function">getMovieByTitle</span><span class="token punctuation">(</span>title<span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">error<span class="token punctuation">,</span> movie</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
            
            <span class="token keyword">if</span><span class="token punctuation">(</span>error <span class="token operator">&amp;&amp;</span> <span class="token operator">!</span>hasSentBackError<span class="token punctuation">)</span><span class="token punctuation">{</span>
                
                hasSentBackError <span class="token operator">=</span> <span class="token boolean">true</span>
                <span class="token function">callback</span><span class="token punctuation">(</span>error<span class="token punctuation">,</span> <span class="token keyword">null</span><span class="token punctuation">)</span>
                
            <span class="token punctuation">}</span><span class="token keyword">else</span> <span class="token keyword">if</span><span class="token punctuation">(</span><span class="token operator">!</span>hasSentBackError<span class="token punctuation">)</span><span class="token punctuation">{</span>
                
                fetchedMovies<span class="token punctuation">.</span><span class="token function">push</span><span class="token punctuation">(</span>movie<span class="token punctuation">)</span>
                
                <span class="token keyword">if</span><span class="token punctuation">(</span>fetchedMovies<span class="token punctuation">.</span>length <span class="token operator">==</span> titles<span class="token punctuation">.</span>length<span class="token punctuation">)</span><span class="token punctuation">{</span>
                    <span class="token function">callback</span><span class="token punctuation">(</span><span class="token keyword">null</span><span class="token punctuation">,</span> fetchedMovies<span class="token punctuation">)</span>
                <span class="token punctuation">}</span>
                
            <span class="token punctuation">}</span>
            
        <span class="token punctuation">}</span><span class="token punctuation">)</span>
        
    <span class="token punctuation">}</span>
    
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br></div></div></template>
<template v-slot:marking-guidelines>
<ul>
<li>2 points for the sequential function</li>
<li>2 points for the parallel function</li>
<li>0 points for a function that assumes callback function runs synchronously (called immediately)</li>
